t:auto;\n            margin-left: 14vw;\n            //padding-left:11vw;\n        \n        }\n\n\n        .text:hover{\n            \n            color:#ffcc33;\n        }\n\n        .text::after{\n            content: '';\n            display:block;\n            margin-top:2px;\n            width:2px;\n            height:2px;\n            background: #ffcc33;\n            transition:width 0.5s;     \n        \n        }\n\n        .text:hover::after{\n\n            width:100%;\n            transition:width 0.5s;\n            \n            \n        }\n    }\n\n    .scroll-nav-active{\n            background-color: rgb(0,0,0,0.9);\n            //box-shadow: 0 3px 5rem rgba(255, 0, 0, 0.2);\n    }\n    \n\n    .grid-object-2{\n        //The quote part\n        grid-area: quote;\n        //border:solid blue;\n        display:flex;\n        flex-direction: column;\n        justify-content:center;\n        align-items: center;\n        padding-top:5vw;\n        padding-bottom: 3vw;\n\n        \n        \n        .quote-items{\n            color:white;    \n        }\n        .sign{\n            width: 5vw;\n            margin-bottom: 7vw;\n\n        }\n        .text{\n            //margin-top: 3vw;\n            text-align: center;\n            font-size: 3vw;\n            margin-bottom: 7vw;\n        }\n        .line{\n            height:1px;\n            width:5vw;\n            border-bottom:solid #ffcc33;\n            border-radius: 2px;\n            margin-bottom:2vw;\n\n        }\n        .name{\n            \n            font-size:2vw;\n        }\n    }\n\n    .grid-object-3{\n        grid-area:empty;\n        \n        \n    }\n}\n\n\n.grid-container-2{\n    background-color: rgb(0, 0, 0);\n    //border:solid green;\n    color:white;\n    display:grid;\n    grid-template-columns: 35vw 65vw;\n    grid-template-rows: 20vw 40vw;\n    grid-template-areas:\n    \"pic title\"\n    \"pic content\";\n    position:relative;\n    \n\n    &::before{\n    content:'';\n    position:absolute;\n    background-image: url(\"../image/index/camera.jpg\");\n    top:0vw;\n    right:0vw;\n    left:20vw;\n    bottom:0vw;\n    background-size:90vw;\n    background-repeat: no-repeat;\n    opacity:0.4;\n    }\n \n\n\n    .grid-object-4{\n        position:relative;\n        grid-area: pic;\n        border:solid #ffcc33;\n        border-radius: 10px 70px 10px 10px;\n        margin-left:5vw;\n        margin-top:10vw;\n        margin-bottom: 10vw;\n        background-image: url(\"../image/index/turtle.jpg\");\n        background-size:cover;\n        background-repeat: no-repeat;\n        background-position:center;\n\n    }\n\n    .grid-object-5{\n        position:relative;\n        grid-area:title;\n        //border:solid red;\n        padding-left: 4vw;\n        padding-top: 3vw;\n        \n        \n\n\n        .line1{\n            font-size:1.5vw;\n        }\n\n        .line2{\n            font-size:6vw;\n            font-weight: bolder;\n            color:#ffcc33;\n        }\n\n        .line3{\n            font-size:6vw;\n            font-weight: bolder;\n            color:#ffcc33;\n        }\n\n    }\n\n    .grid-object-6{\n        position:relative;\n        grid-area:content;\n        //border:solid yellow;\n        padding-left: 4vw;\n        padding-right:4vw;\n        \n\n        }\n        .content{\n            font-size: 2vw;\n            margin-bottom: 2vw;\n\n        }\n}"],"sourceRoot":""}]);
